import { Metadata } from "next";
import { redirect } from "next/navigation";
import { getServerSession } from "next-auth";
import { prisma } from "@/utils/prismaDB";
import { authOptions } from "@/utils/auth";
import LinkWorkspace, {
  LinkRecord,
} from "@/components/Dashboard/LinkWorkspace";
import AdminUserPanel from "@/components/Dashboard/AdminUserPanel";

export const metadata: Metadata = {
  title: "Dashboard | click.aiesec.my",
  description: "Create, manage, and track branded short links.",
};

export default async function DashboardPage() {
  const session = await getServerSession(authOptions);
  if (!session?.user?.id) {
    redirect("/signin");
  }

  const isAdmin = session.user.role === "ADMIN";
  const whereLinks = isAdmin ? {} : { userId: session.user.id as string };
  const clickWhere = isAdmin ? {} : { shortLink: { userId: session.user.id } };
  const last24h = new Date(Date.now() - 24 * 60 * 60 * 1000);

  const [links, linkCount, totalClicks, clicksLast24h, topLinks] =
    await Promise.all([
      prisma.shortLink.findMany({
        where: whereLinks,
        include: {
          user: { select: { id: true, name: true, email: true } },
          _count: { select: { clickEvents: true } },
        },
        orderBy: { createdAt: "desc" },
      }),
      prisma.shortLink.count({ where: whereLinks }),
      prisma.clickEvent.count({ where: clickWhere }),
      prisma.clickEvent.count({
        where: { ...clickWhere, createdAt: { gte: last24h } },
      }),
      prisma.shortLink.findMany({
        where: whereLinks,
        take: 5,
        orderBy: { clickEvents: { _count: "desc" } },
        include: { _count: { select: { clickEvents: true } } },
      }),
    ]);

  const serializedLinks: LinkRecord[] = links.map((link) => ({
    id: link.id,
    slug: link.slug,
    destination: link.destination,
    title: link.title,
    isActive: link.isActive,
    clickCount: link._count.clickEvents,
    createdAt: link.createdAt.toISOString(),
    updatedAt: link.updatedAt.toISOString(),
    user: isAdmin ? link.user : undefined,
  }));

  const overview = {
    linkCount,
    totalClicks,
    clicksLast24h,
    topLinks: topLinks.map((link) => ({
      id: link.id,
      slug: link.slug,
      title: link.title,
      clickCount: link._count.clickEvents,
    })),
  };

  return (
    <section className="bg-[#F4F7FF] px-6 pb-20 pt-24 dark:bg-dark md:px-10 lg:px-14 lg:pb-24 lg:pt-28">
      <div className="container">
        <div className="mb-8 flex flex-col gap-2">
          <p className="text-sm uppercase tracking-wide text-primary">
            click.aiesec.my
          </p>
          <h1 className="text-3xl font-bold text-dark dark:text-white">
            URL shortener workspace
          </h1>
          <p className="text-body-secondary text-sm">
            Create branded links, track performance, and keep everything under
            control. {isAdmin ? "Admins see all users and links." : "Only your links are visible here."}
          </p>
        </div>

        <LinkWorkspace
          initialLinks={serializedLinks}
          overview={overview}
          isAdmin={isAdmin}
        />

        {isAdmin && (
          <div className="mt-8">
            <AdminUserPanel />
          </div>
        )}
      </div>
    </section>
  );
}
